import React, { useEffect, useState } from 'react'
import './App.css'

import { trebleNotes } from './constants/notes'


const App = () => {


  const randomIndex = (avoid = null) => {
    const r = Math.random() * trebleNotes.length | 0

    if (avoid && r === avoid) return randomIndex(avoid)

    return r
  }

  const [noteIndex, setNoteIndex] = useState(randomIndex())

  const [possibleAnswers, setPossibleAnswers] = useState(['E', 'F', 'G', 'A', 'B', 'C', 'D'])

  const changeNote = () => {
    const r = randomIndex(noteIndex)

    setNoteIndex(r)
    shuffleArray(possibleAnswers)
  }


  const shuffleArray = (arr) => {
    for (let i = 0; i < arr.length; i++) {
      const r = Math.random() * arr.length | 0

      const temp = arr[i]
      arr[i] = arr[r]
      arr[r] = temp
    }
  }

  const mapToComponent = () => {
    return React.createElement(trebleNotes[noteIndex].component)
  }

  const checkAnswer = (isCorrect) => {
    if (isCorrect) {
      changeNote()
    }
  }



  return (
    <section className="main-container">
      <h1>Choose the correct note for the treble clef</h1>
      <div className="question-container">
        {
          mapToComponent()
        }
      </div>

      <div className="options-container">
        {
          possibleAnswers.map(option => (
            <div key={option} className={`option ${option === trebleNotes[noteIndex].note.toUpperCase()
              ? 'correct' : 'wrong'}`}
              onClick={checkAnswer.bind(this, option === trebleNotes[noteIndex].note.toUpperCase())}>
              {option}
            </div>
          ))
        }
      </div>
    </section>
  )
}

export default App